:root {
  --font-roboto: "Roboto", sans-serif;
}

:root {
  --tomato: hsl(4, 100%, 67%);
  --dark-slate-grey: hsl(234, 29%, 20%);
  --charcoal-grey: hsl(235, 18%, 26%);
  --grey: hsl(231, 7%, 60%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-roboto);
  background-color: var(--charcoal-grey);
  color: var(--white);
}

/*$breakpoints-up: (
    "medium": "40em",
    "large": "71.875em",
    "x-large": "87.5em",
);

// 693px, 1149px, 1399px
$breakpoints-down: (
    "small": "39.9375em",
    "medium": "71.8125em",
    "large": "87.4375em",
);*/
h1 {
  margin-top: 0;
  padding-top: 15.625rem;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1.5rem;
}
@media (min-width: 64em) {
  h1 {
    padding-top: 0;
    font-size: 2.375rem;
    font-weight: 700;
  }
}

@media (min-width: 40em) {
  p {
    margin-right: 21.875rem;
  }
}

ul {
  list-style-type: none;
}

li {
  margin-bottom: 1rem;
  margin-left: -2.5rem;
}
@media (min-width: 40em) {
  li {
    margin-bottom: 1rem;
    margin-left: -2.5rem;
  }
}

.container {
  background-color: var(--white);
  /*background-image: url("/assets/images/illustration-sign-up-mobile.svg");
  background-repeat: no-repeat;
  background-size: contain;*/
  margin: 0 auto;
  max-width: 50rem;
  padding: 1.5625rem;
}
@media (min-width: 64em) {
  .container {
    padding: 1.25rem;
    background-color: var(--charcoal-grey);
    background-image: none;
  }
}

.signup-form {
  color: var(--charcoal-grey);
}
@media (min-width: 64em) {
  .signup-form {
    background-color: var(--white);
    /*background-image: url("/assets/images/illustration-sign-up-desktop.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right;*/
    border-radius: 1.25rem;
    height: 82vh;
    margin: 5rem auto 0;
    padding: 3.75rem 2.5rem;
  }
  .signup-form input[type=email] {
    display: flex;
  }
}

.footer {
  margin: 1.25rem;
}

label {
  display: block;
  margin: 0.5rem 0;
  font-weight: 700;
  font-size: 0.8125rem;
}

input,
button {
  margin: 0.625rem 0;
  border: 0.0625rem solid var(--grey);
  border-radius: 0.3125rem;
  width: 100%;
  min-height: 3.5em;
}
@media (min-width: 64em) {
  input,
  button {
    margin: 0.625rem 0;
    border: 0.0625rem solid var(--grey);
    border-radius: 0.3125rem;
    width: 50%;
    min-height: 3.5em;
  }
}

button {
  background-color: var(--dark-slate-grey);
  color: var(--white);
  font-weight: 700;
}

button:hover {
  background-color: var(--tomato);
  box-shadow: 0 0.625rem 1.875rem var(--tomato);
  border: none;
}

.success-message {
  background-color: var(--white);
  color: var(--charcoal-grey);
}
@media (min-width: 64em) {
  .success-message {
    background-color: var(--charcoal-grey);
    height: 60vh;
    margin: 6.25rem;
    padding: 1.25rem;
    border-radius: 1.25rem;
  }
}

@media (min-width: 64em) {
  .success-form {
    background-color: var(--white);
    height: 70vh;
    width: 35%;
    margin: 0 auto;
    padding: 1.25rem;
    border-radius: 1.25rem;
  }
}

@media (min-width: 64em) {
  .success-form img {
    padding-top: 2.5rem;
    padding-left: 1.25rem;
  }
}

@media (min-width: 64em) {
  .success-form h2 {
    padding: 0 1.25rem;
  }
}

@media (min-width: 64em) {
  .success-form p {
    padding: 0 1.25rem;
    width: 100%;
  }
}

@media (min-width: 64em) {
  .success-form button {
    margin-left: 1.25rem;
    width: 85%;
  }
}

.image__desktop {
  display: none;
}
@media (min-width: 64em) {
  .image__desktop {
    display: block;
    top: 0;
    position: absolute;
    margin-top: 7.5rem;
    margin-left: 25rem;
    width: 21.25rem;
  }
}
.image__mobile {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
@media (min-width: 64em) {
  .image__mobile {
    display: none;
  }
}